
.star {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.star .star-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 1.2rem;
  background-repeat: no-repeat;
  background-size: content;
  /*数组中的字符串拼接成以下的类名，就可以实现数组控制样式*/
}
.star .star-item.star-on {
  background-image: url('star24_on@2x.png');
}
.star .star-item.star-half {
  background-image: url('star24_half@2x.png');
}
.star .star-item.star-off {
  background-image: url('star24_off@2x.png');
}



